@import 'globals';
@import 'variables';

.single-grid{
    margin-top: 160px;
    .wraper-single-grid{
        padding: 0 48px;
        .inside-single-wrap{
            width: 100%;
            .wraper-2-text{
                width: 100%;
                position: relative;
                p{
                    margin: 0;
                    font-family: 'Aeonik-Regular', sans-serif;
                    color: $priamry_color;
                    font-size: 32px;
                    line-height: 36px;
                    font-weight: 400;
                    max-width: 1210px;
                    text-indent: 29%;
                    margin-left: auto;
                    margin-top: 64px;
                }
                .img-wrap-1{
                    max-width: 1210px;
                    max-height: 860px;
                    margin-left: auto;
                    position: relative;
                    overflow: hidden;
                    img{
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }
                    .plyr__video-embed{
                        iframe{

                        }
                    }
                }
                .img-wrap-2{
                    max-width: 445px;
                    max-height: 620px;
                    height: 620px;
                    overflow: hidden;
                    position: absolute;
                    bottom: 0;
                    width: 445px;
                    img{
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }
                    .plyr{
                        height: 100%;
                        width: 100%;
                        iframe{

                        }
                    }
                    .plyr__video-embed{
                        iframe{

                        }
                    }
                }
            }
            .full-section{
                margin-top: 10%;
                width: 100%;
                height: 900px;
                max-height: 900px;
                position: relative;
                overflow: hidden;
                img{
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
                .plyr__video-embed{
                    iframe{

                    }
                }
            }
            .text-section{
                margin-top: 60px;
                width: 100%;
                display: flex;
                gap: 60px;
                p{
                    max-width: 30%;
                    font-family: 'Aeonik-Regular', sans-serif;
                    color: $priamry_color;
                    font-size: 18px;
                    line-height: 25.8px;
                    font-weight: 400;
                }
            }
            .wraper-grid-3{
                position: relative;
                margin-top: 240px;
                .img-M{
                    width: 37.2%;
                    margin-bottom: 140px;
                    position: relative;
                    overflow: hidden;
                    img{
                        width: 100%;
                        height:620px;
                        max-height: 620px;
                        object-fit: cover;
                    }   
                    .video-wrap{
                        .plyr__video-embed{
                            iframe{

                            }
                        }
                    }  
                }
                .img-S{
                    width: 25%;
                    height:620px;
                    position: relative;
                    overflow: hidden;
                    img{
                        width: 100%;
                        height:620px;
                        max-height: 620px;
                        object-fit: cover;
                    }
                    .video-wrap{
                        height: 100%;
                        .plyr{
                            height: 100%;
                            iframe{
                                
                            }
                        }
                        .plyr__video-embed{
                            height: 100%;
                            iframe{
                                
                            }
                        }
                    }    
                }
                .img-L{
                    width: 65%;
                    margin-bottom: 140px;
                    position: relative;
                    overflow: hidden;
                    img{
                        width: 100%;
                        max-height: 860px;
                        height:860px;
                        object-fit: cover;
                    }
                    .video-wrap{
                        .plyr__video-embed{
                            iframe{
                                
                            }
                        }
                    } 
                }
            }
            .wraper-grid-4{
                position: relative;
                margin-bottom: 14% !important;
                .img-M{
                    width: 37.2%;
                    text-decoration: none;
                    position: relative;
                    overflow: hidden;
                    img{
                        width: 100%;
                        height:620px;
                        max-height: 620px;
                        object-fit: cover;
                    }
                    .video-wrap{
                        .plyr__video-embed{
                            iframe{
                                
                            }
                        }
                    } 
                }
                .img-S{
                    width: 25%;
                    text-decoration: none;
                    height:620px;
                    position: relative;
                    overflow: hidden;
                    img{
                        width: 100%;
                        height:620px;
                        max-height: 620px;
                        object-fit: cover;
                    }
                    .video-wrap{
                        height: 100%;
                        .plyr{
                            height: 100%;
                            width: 100%;
                            iframe{
                                
                            }
                        }
                        .plyr__video-embed{
                            iframe{
                                
                            }
                        }
                    } 
                }
            }
            .project-pagination{
                display: flex;
                justify-content: space-between;
                align-items: center;
                margin-bottom: 7%;
                padding-top: 4%;
                .wraper-pagination{
                    a{
                        text-decoration: none;
                        .back{
                            font-family: 'Defectica-Regular', sans-serif;
                            color: $priamry_color;
                            font-size: 60px;
                            line-height: 72px;
                            font-weight: 400;
                            margin: 0;
                        }
                        .next-title{
                            font-family: 'Aeonik-Regular', sans-serif;
                            color: $priamry_color;
                            font-size: 18px;
                            line-height: 21.6px;
                            font-weight: 400;
                            margin: 0;
                        }
                    }
                }
                .wraper-pagination-1{
                    text-align: end;
                    a{
                        text-decoration: none;
                        .back{
                            font-family: 'Defectica-Regular', sans-serif;
                            color: $priamry_color;
                            font-size: 60px;
                            line-height: 72px;
                            font-weight: 400;
                            margin: 0;
                        }
                        .next-title{
                            font-family: 'Aeonik-Regular', sans-serif;
                            color: $priamry_color;
                            font-size: 18px;
                            line-height: 21.6px;
                            font-weight: 400;
                            margin: 0;
                        }
                    }
                }
            }
            .justify-center{
                justify-content: center;
            }
            .justify-end{
                justify-content: end;
            }
        }
    }
}





