.wrap-products {
    position: relative;
    overflow: hidden; 
    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0; 
        width: 100%;
        height: 1px;
        background-color: $priamry_color; 
        transform: scaleX(0); 
        transform-origin: left;
        transition: transform .6s linear;
        transition-delay: .6s;
    }

    &.is-inview::before {
        transform: scaleX(1); 
    }
}

@keyframes border-animation {
    0% {
        transform: scaleX(0); 
    }
    100% {
        transform: scaleX(1); 
    }
}

